const state = {
    products: [
      {
        name: "Fluffball",
        img: "http://placekitten.com/150/150?image=1",
        smallImg: "http://placekitten.com/50/50?image=1",
        price: 0.99,
        count: 0,
      },
      {
        name: "General Mayhem",
        img: "http://placekitten.com/150/150?image=2",
        smallImg: "http://placekitten.com/50/50?image=2",
        price: 3.14,
        count: 0,
      },
      {
        name: "Nyan Cat",
        img: "http://placekitten.com/150/150?image=3",
        smallImg: "http://placekitten.com/50/50?image=3",
        price: 2.73,
        count: 0,
      },
    ],
  
    viewCart: false,
  
    getCartCount: function() {
      return this.products.reduce((total, { count }) => total + count, 0);
    },
  
    getPricePerProduct: function(index) {
      const { count, price } = this.products[index];
      return (count * price).toFixed(2);
    },
  
    getTotalPrice: function() {
      return this.products.reduce((total, { count, price }) => total + (count * price), 0).toFixed(2);
    },
  };
  
  export default state;
  